.animateOnLoad {
  opacity: 0;
  transform: translate(0, -10px);
  transition: .7s cubic-bezier(.67,.02,.27,.95);

  &--ltr {
    transform: translate(-30px, 0);
  }

  &--rtl {
    transform: translate(30px, 0);
  }

  &--down {
    transform: translate(0, -10px);
  }

  &--up {
    transform: translate(0, 10px);
  }

  &--opaque {
    opacity: 1;
  }

  &.inView {
    opacity: 1;
    transform: translate(0, 0);
  }
}

.animateManual {
  transition: .7s cubic-bezier(.67,.02,.27,.95);
}

[data-animate-delay="50"].animateOnLoad,[data-animate-delay="50"].animateManual {
  transition-delay: 50ms;
}

