@keyframes bounce {
  0% {
    bottom: 0px;
  }

  5% {
    bottom: -15px;
  }

  10% {
    bottom: -8px;
  }

  12.5% {
    bottom: 0px;
  }

  100% {
    bottom: 0px;
  }
}